    export default {
        data() {
            return {
                detail: {
                    start_time: '',
                    end_time: ''
                },
                msg: null,
                is_clerk: 0,
                submit: false,
                cardId: null,
                surplus_number: 0,
                useNumber: '',
                qrCodeId: -1,
            }
        },
        name: "clerk",
        methods: {
            getList(id) {
                let that = this;
                that.$showLoading({
                    text: '加载中...'
                });
                that.$request({
                    url: that.$api.card.detail,
                    data: {
                        cardId: id,
                        qr_code_id: that.qrCodeId,
                    },
                }).then(response=>{
                    that.$hideLoading();
                    if(response.code === 0) {
                        that.detail = response.data.card;
                        that.cardId = id;
                    } else {
                        uni.showToast({
                            title: response.msg,
                            icon: 'none',
                            duration: 1000,
                        });
                    }

                    if (that.detail.clerk_number && that.detail.clerk_number >= 1) {
                        uni.showModal({
                            title: '提示',
                            content: '核销码已失效',
                            showCancel: false,
                            complete: function(res) {
                                uni.navigateBack();
                            }
                        });
                    };
                }).catch(() => {
                    that.$hideLoading();
                });
            },
            clerk() {
                let that = this;
                uni.showLoading({
                    title: '核销中...'
                });

                if (!that.useNumber) {
                    uni.showToast({
                        title: '请输入核销次数',
                        icon: 'none',
                        duration: 2000,
                    });
                    return false;
                }

                that.$request({
                    url: that.$api.card.clerk,
                    data: {
                        cardId: that.cardId,
                        use_number: that.useNumber,
                        qr_code_id: that.qrCodeId,
                    },
                }).then(response=>{
                    uni.hideLoading();
                    if(response.code === 0) {
                        that.is_clerk = response.data.is_clerk;
                        that.msg = response.msg;
                        that.submit = false;
                        that.surplus_number = response.data.surplus_number;
                    } else {
                        uni.showToast({
                            title: response.msg,
                            icon: 'none',
                            duration: 2000,
                        });
                    }
                }).catch(() => {
                    uni.hideLoading();
                });
            },
            closeDialog() {
                if (this.msg) {
                    this.msg = ''
                    if (this.is_clerk) {
                        let url = '';
                        if (this.surplus_number > 0) {
                            url = '/plugins/clerk/order/order?status=2&type=0';
                        } else {
                            url = '/plugins/clerk/order/order?status=2&type=1';
                        }
                        uni.navigateBack({});
//                        uni.redirectTo({
//                            url: url
//                        })
                    }
                } else {
                    this.msg = ''
                }
            },
        },
        onLoad(options) { this.$commonLoad.onload(options);
            if(options.qr_code_id) {
                this.qrCodeId = options.qr_code_id;
            }
            this.getList(options.cardId);
        }
    }
